<template>
    <b-card>
        <h2 class="text-center"></h2>
        <span> - ,     ,  
                      . ,       
                    ,   . ,  ,   
                      .   ,     </span>

        <hr>
        <h2>:</h2>

        <div class="mt-2 grid-cards-columns grid-cards-columns-small">
            <b-card
                v-for="certificate of getCertificates"
                class="mb-2 card-item"
                :title="certificate.name"
                :img-src="`/storage/certificates/${certificate.grade}.jpg`"
                :img-alt="certificate.name"
                tag="article"

                @click="()=>redirect(certificate.id)"
                :key="certificate.id"
            >
                <b-card-text>
                    <b-badge variant="success">   {{ certificate.fertility_bonus }}%</b-badge>
                    <b-badge variant="warning">   {{ certificate.fertility_bonus }}%</b-badge>
                    <b-badge variant="primary"> {{ certificate.price }}</b-badge>
                </b-card-text>

                <!--                    <b-button class="card-btn" href="#" variant="primary"> </b-button>-->
            </b-card>
        </div>
    </b-card>
</template>

<script>
import {mapActions, mapGetters} from 'vuex';

export default {
    name    : "Store",
    computed: {
        ...mapGetters([
            'getCertificates',
        ])
    },
    methods : {
        redirect(id) {
            this.$router.push(`/certificates/${id}`)
        },
        ...mapActions(['fetchCertificates'])
    },
    mounted() {
        this.fetchCertificates();
    },
}
</script>

