<template>
    <div class="p-3">
        <b-form
            @submit.prevent="onSubmit"
            enctype="multipart/form-data"
        >
            <b-alert variant="danger" :show="error"> - </b-alert>

            <!--      NAME      -->
            <b-form-group
                id="input-name"
                label=" :"
                label-for="name"
            >
                <b-form-input
                    id="name"
                    v-model="form.name"
                    type="text"
                    placeholder=''
                    required
                    minLength="3"
                ></b-form-input>
            </b-form-group>

<!--            &lt;!&ndash;      DESCRIPTION      &ndash;&gt;-->
<!--            <b-form-group id="input-description" label=" :" label-for="description">-->
<!--                <b-form-input-->
<!--                    id="description"-->
<!--                    v-model="form.description"-->
<!--                    placeholder=' " " -  "'-->
<!--                    required-->
<!--                    minLength="7"-->
<!--                ></b-form-input>-->
<!--            </b-form-group>-->

            <!--      IMAGE      -->
            <b-form-group id="input-image" label=" :" label-for="image">
                <b-form-file
                    accept="image/jpeg, image/png, image/gif"
                    v-model="form.image"
                    :state="Boolean(form.image)"
                    placeholder="     ..."
                    drop-placeholder="  ..."
                ></b-form-file>
                <p class="mt-2">Selected file: <b>{{ form.image ? form.image.name : '' }}</b></p>
            </b-form-group>

            <!--    efficiency    -->
            <b-form-group
                id="input-efficiency"
                :label="` :`"
                label-for="efficiency"
                description=" .    "
            >
                <b-form-input
                    id="efficiency" v-model="form.efficiency" type="number" min="0" max="1000000000"
                ></b-form-input>
            </b-form-group>

            <!--    price    -->
            <b-form-group
                id="input-price"
                :label="` :`"
                label-for="price"
                description="    ( ,    )"
            >
                <b-form-input id="price" v-model="form.price" type="number" min="0"></b-form-input>
            </b-form-group>

            <!--    donate_price    -->
            <b-form-group
                id="input-donate_price"
                :label="`  ( ):`"
                label-for="donate_price"
                description="   "
            >
                <b-form-input id="donate_price" v-model="form.donate_price" type="number" min="0"></b-form-input>
            </b-form-group>

            <b-button type="submit" variant="primary"></b-button>
            <!--            <b-button type="reset" variant="danger"></b-button>-->
        </b-form>

    </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
    name: "CreateShovel",
    data() {
        return {
            form : {
                id            : this.$route.query.id || null,
                name          : this.$route.query.name || '',
                // description   : this.$route.query.description || 'OOO " " -    ',
                image         : null,
                imagePath     : this.$route.query.image || '',
                efficiency    : this.$route.query.efficiency || 100,
                price         : this.$route.query.price || 0,
                donat_price   : this.$route.query.price || 0,
            },
            error: false
        }
    },
    computed: mapGetters(['getShovel']),
    methods: {
        ...mapActions(['createShovel', 'updateShovel']),
        async onSubmit() {
            // there are parameters, so need to update the seller
            if (Object.keys(this.$route.query).length !== 0) {
                // if the image is already there, then we replace it
                let form = {
                    ...this.form,
                    image: this.form.image || this.form.imagePath
                };
                delete form.imagePath; // remove unnecessary

                // update birds, check errors
                this.error = !(await this.updateShovel(form));
            } else {
                // create seller, check errors
                this.error = !(await this.createShovel(this.form));
            }

            // redirect to admin page
            if (!this.error) this.$router.push({name: 'admin-shovels'})
        },
    },
}
</script>

