<template>
    <div class="p-3">
        <b-form
            @submit.prevent="onSubmit"
            enctype="multipart/form-data"
        >
            <b-alert variant="danger" :show="error"> - </b-alert>

            <!--      NAME      -->
            <b-form-group
                id="input-name"
                label=" :"
                label-for="name"
            >
                <b-form-input
                    id="name"
                    v-model="form.name"
                    type="text"
                    placeholder=""
                    required
                    minLength="3"
                ></b-form-input>
            </b-form-group>

            <!--      DESCRIPTION      -->
            <b-form-group id="input-description" label=" :" label-for="description">
                <b-form-input
                    id="description"
                    v-model="form.description"
                    placeholder=" "
                    required
                    minLength="7"
                ></b-form-input>
            </b-form-group>

            <!--      IMAGE      -->
            <b-form-group id="input-image" label=" :" label-for="image">
                <b-form-file
                    accept="image/jpeg, image/png, image/gif"
                    v-model="form.image"
                    :state="Boolean(form.image)"
                    placeholder="     ..."
                    drop-placeholder="  ..."
                ></b-form-file>
                <p class="mt-2">Selected file: <b>{{ form.image ? form.image.name : '' }}</b></p>
            </b-form-group>

            <!--    DEMAND ()    -->
            <b-form-group
                id="input-demand"
                :label="`    :`"
                label-for="demand"
                description="       "
            >
                <b-form-input id="demand" v-model="form.demand" type="number" min="0" max="100000"></b-form-input>
            </b-form-group>

            <!--    fertility ()    -->
            <b-form-group
                id="input-fertility"
                :label="`:`"
                label-for="fertility"
                description="     "
            >
                <b-form-input
                    id="fertility" v-model="form.fertility" type="number" min="0"
                    max="100000000"
                ></b-form-input>
            </b-form-group>

            <!--    care ()    -->
            <b-form-group
                id="input-care"
                :label="`  :`"
                label-for="care"
                description="  %       "
            >
                <b-form-input id="care" v-model="form.care" type="number" min="-100" max="100"></b-form-input>
            </b-form-group>

            <!--    litter ()    -->
            <b-form-group
                id="input-litter"
                :label="`:`"
                label-for="litter"
                description="      "
            >
                <b-form-input id="litter" v-model="form.litter" type="number" min="0" max="10000000"></b-form-input>
            </b-form-group>

            <b-form-group
                id="input-sellers"
                :label="`    :`"
                label-for="sellers"
            >
                <b-form-checkbox
                    v-for="seller of getSellers"
                    :id="`sellers-${seller.id}`"
                    :name="`sellers-${seller.id}`"
                    v-model="form.sellers"

                    :key="seller.id"
                    :value="seller.id"
                >
                    {{ seller.name }}
                </b-form-checkbox>
            </b-form-group>

            <!--    price    -->
            <b-form-group
                id="input-egg-price"
                :label="` : `"
                label-for="egg-price"
            >
                <b-form-input id="egg-price" v-model="form.egg_price" type="number" min="0"></b-form-input>
            </b-form-group>

            <!--    price    -->
            <b-form-group
                id="input-price"
                :label="`:`"
                label-for="price"
            >
                <b-form-input id="price" v-model="form.price" type="number" min="0"></b-form-input>
            </b-form-group>
            <b-button type="submit" variant="primary">{{
                    Object.keys($route.query).length == 0 ? ' )' :
                        ''
                }}
            </b-button>
            <!--            <b-button type="reset" variant="danger"></b-button>-->
        </b-form>
    </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
    name: "CreateBird",
    data() {
        return {
            form : {
                id         : this.$route.query.id || null,
                name       : this.$route.query.name || 'Tester',
                description: this.$route.query.description || ' ',
                image      : null,
                imagePath  : this.$route.query.image || '',
                demand     : this.$route.query.demand || 50,
                fertility  : this.$route.query.fertility || 1,
                care       : this.$route.query.care || 10,
                litter     : this.$route.query.litter || 10,
                sellers    : this.$route.query.sellers ? JSON.parse(this.$route.query.sellers).map(seller => seller.id)
                    : [],
                egg_price  : this.$route.query.egg_price || 1,
                price      : this.$route.query.price || 100
            },
            error: false
        }
    },
    computed: {
        ...mapGetters(['getSellers'])
    },
    methods : {
        ...mapActions(['createBird', 'updateBird', 'fetchSellers']),

        async onSubmit() {
            // there are parameters, so need to update the bird
            if (Object.keys(this.$route.query).length !== 0) {
                // if the image is already there, then we replace it
                let form = {
                    ...this.form,
                    image: this.form.image || this.form.imagePath
                };
                delete form.imagePath; // remove unnecessary

                this.error = !(await this.updateBird(form));
            } else {
                // check errors
                this.error = !(await this.createBird(this.form));
            }

            // redirect to admin page
            if (!this.error) await this.$router.push({name: 'admin-birds'})
        },

    },
    mounted() {
        this.fetchSellers();
    }
}
</script>

