<template>
        <b-card>
            <h2 class="text-center"></h2>
            <span>   ,     </span>

            <hr>
            <h2>:</h2>

            <div class="mt-2 grid-cards-columns">
                <b-card
                    v-for="shovel of getShovels"
                    class="mb-2 card-item"
                    :title="shovel.name"
                    :img-src="`/storage/${shovel.image}`"
                    :img-alt="shovel.name"
                    img-top
                    tag="article"

                    @click="()=>redirect(shovel.id)"
                    :key="shovel.id"
                >
                    <b-card-text class="d-flex justify-content-between flex-wrap">
                        <b-badge variant="success" class="mb-1">: {{shovel.efficiency}}.</b-badge>
                        <b-badge v-if="shovel.price ? true : false" variant="primary"
                                 class="d-flex align-items-center justify-content-center">: {{shovel
                            .price}}</b-badge>
                        <b-badge v-else variant="primary">  : {{shovel.donate_price}} .</b-badge>
                    </b-card-text>

                    <!--                    <b-button class="card-btn" href="#" variant="primary"> </b-button>-->
                </b-card>
            </div>
        </b-card>
</template>

<script>
import {mapActions, mapGetters} from 'vuex';

export default {
    name: "Store",
    mounted() {
        this.fetchShovels();
    },
    computed: {
        ...mapGetters([
            'getShovels',
        ])
    },
    methods : {
        redirect(id) {
            this.$router.push(`/shovels/${id}`)
        },
        ...mapActions(['fetchShovels'])
    }
}
</script>

